.main {
  position: relative;
  top: -135px;
  left: 0px;
  width: 100%;
  height: 0;
  padding-top: 56.1%;
  background-image: url("../static/images/AboutRLRC.png");
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 1;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.selectionbar_menu {
  color: #ffffff;
  width: auto;
}
.selection_line_white {
  position: relative;
  top: 2px;
  background-color: #ffffff;
  width: 190px;
  height: 2px;
  z-index: 10;
  /* top: 247.5px; */
}
.selection_line_grey {
  background-color: rgba(221, 221, 221, 0.674);
  width: 630px;
  height: 2px;
  /* top: 247.5px; */
}

#selectbar_content {
  float: left;
  margin-top: 21.5px;
  position: relative;
  width: auto;
  height: 19px;
  text-align: left;
  font: normal normal 16px/19px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
    sans-serif, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
  letter-spacing: 0px;
  opacity: 1;
  margin-left: 40px;
  margin-right: 80px;
}
#selectbar_content.select_new_notice {
  width: auto;
}
a:visited {
  color: #ffffff;
}
.vision_mission {
  position: relative;
  top: -135px;
  width: 100%;
  height: 0;
  padding-top: 56.5%;
  background-image: url("../static/images/VISIONMISSION.png");
  background-repeat: no-repeat;
  background-size: cover;
  /* UI Properties */
  opacity: 1;
  flex-direction: row;
}

.material_group {
  width: 100%;
  height: auto;
  background: white 0% 0% no-repeat padding-box;
  opacity: 1;
}
.material_title {
  position: relative;
  left: -25px;
  width: auto;
  height: auto;
  text-align: center;
  font: normal normal bold 42px sans-serif;
  letter-spacing: 0px;
  color: #191919;
  opacity: 1;
  z-index: 1;
}
.meterial_carousel {
  position: relative;
  margin-top: 135px;
  display: flex;
  justify-content: center;
  width: 100%;
}

.part_group {
  width: 100%;
  height: auto;
  margin-top: 270px;
  background: white 0% 0% no-repeat padding-box;
  opacity: 1;
}
.part_title {
  position: relative;
  width: auto;
  height: auto;
  text-align: center;
  left: -25px;
  font: normal normal bold 42px sans-serif;
  letter-spacing: 0px;
  color: #191919;
  opacity: 1;
  z-index: 1;
}
.part_carousel {
  width: 100%;
  margin-top: 135px;
  display: flex;
  justify-content: center;
}

.key_project {
  position: relative;
  top: -135px;
  width: 100%;
  background: #ffffff 0% 0% no-repeat padding-box;
  opacity: 1;
}
.key_project_title {
  position: relative;
  text-decoration: none;
  top: 135px;
  text-align: center;
  width: auto;
  font: normal normal bold 40px/36px sans-serif;
  letter-spacing: 0px;
  color: black;
  opacity: 1;
}
td {
  width: 50%;
}
center {
  height: 60%;
}
.key_project_contents {
  position: relative;
  top: calc(135px * 2);
  height: auto;
  left: 3.5%;
  margin-left: auto;
  margin-right: auto;
  display: inline-flex;
  flex-basis: 10%;
  flex-direction: row;
}
